<template>
  <div>
    <el-form 
      ref="form" 
      :model="form" 
      label-width="80px">

      <el-form-item label="价格类型">
        <el-radio-group v-model="form.price_type">
          <el-radio 
            value="单价"
            label="price">单价</el-radio>
          <el-radio 
            value="总价"
            label="total-price">总价</el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="数据类型">
        <el-radio-group v-model="form.data_type">
          <el-radio 
            value="单价" 
            label="avg">均值</el-radio>
          <el-radio 
            value="总价" 
            label="sum">总值</el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="时间范围">
        <el-date-picker
          v-model="form.date_range"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期" 
          value-format="yyyy-MM-dd"
          :picker-options="pickerOptions1" />
      </el-form-item>

      <el-form-item label="所在区域">
        <el-checkbox-group v-model="form.area">
          <el-checkbox 
            v-for="area in area_items" 
            :label="area.area" 
            :key="area.area" 
            name="area.area">{{ area.area }}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>



      <el-form-item>
        <el-button 
          type="primary" 
          @click="get_area_agg">提交查询</el-button>
        <el-button @click="clear_data">清空数据</el-button>
      </el-form-item>
    </el-form>

    <v-chart 
      :force-fit="true"
      :height="height"
      :data="chartData"
      :dataPre="dataPre"
      :scale="scale">
      <v-tooltip />
      <v-axis />
      <v-legend />
      <v-line 
        :position="'crawl_date*price'" 
        :color="'city'" />
      <v-point 
        :position="'crawl_date*price'" 
        :color="'city'" 
        :size="4" 
        :v-style="style" />
    </v-chart>
  </div>
</template>

<script>
  export default {
    name: 'house_charts',
    data () {
      return {
        chartData: [],
        dataPre: {},
        scale: [],
        height: 400,
        style: { stroke: '#fff', lineWidth: 1 },
        form: {
          price_type: 'price',
          data_type: 'avg',
          date_range: '',
          area: []
        },
        area_items: [],
        pickerOptions1: {
          disabledDate(time){
            return time.getTime() > Date.now() && time.getTime() < Date.parse('2017-11-27');
          }
        }
      }
    },
    mounted () {
      this.get_area_data()
    },
    methods: {
      get_area_data: function () {
        this.$http.get('/api/get_area').then(
            response => {
              this.area_items = response.data.items
            }
          )
      },
      get_area_agg: function () {
        var formData = JSON.stringify(this.form)
        this.$http.post('/api/get_area_agg', formData).then(response => {
          if (response.data.total > 0) {
            var fields = new Array([])
            this.chartData = response.data.items
            for (var key in response.data.items[0]) {
              if (key !== 'crawl_date') {
                fields.push(key)
              }
            }
            fields.shift()

            this.dataPre = {
              transform: {
                type: 'fold',
                fields: fields,
                key: 'city',
                value: 'price'
              }
            }
              
            this.scale = [{
              dataKey: 'crawl_date',
                  // min: 0,
              max: 1
            }]
          }
        }
      )},
      clear_data: function () {
        this.form.price_type = 'price'
        this.form.date_range = ''
        this.form.area = []
      }
    }
  }
</script>



