<template>
  <div id="app">
    <el-menu
      class="el-menu-demo"
      mode="horizontal"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b">
      <el-menu-item index="2" @click="go('/')">首页 </el-menu-item>
      <el-menu-item index="1" @click="go('/house_charts')">房价趋势</el-menu-item>
      <el-menu-item index="3" @click="go('/house_search')">房源搜索</el-menu-item>
    </el-menu>
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'app',
    data () {
      return {
        theme1: 'dark'
      }
    },
    methods: {
      go: function (val){
        this.$router.push({path: val})
      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    /*margin-top: 30px;*/
    height: 50px;
  }
  a {
    text-decoration-line: none;
  }
</style>



