import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import house_charts from '@/components/house_charts'
import house_search from '@/components/house_search'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
    },
    {
      path: '/house_charts',
      name: 'house_charts',
      component: house_charts
    },
    {
      path: '/house_search',
      name: 'house_search',
      component: house_search
    }
  ]
})



