<template>
	<div>
		<el-date-picker
      		v-model="crawl_date"
      type="date"
      placeholder="请选择数据抓取的日期"
      :picker-options="pickerOptions1">
    </el-date-picker>
	<el-autocomplete
  v-model="area"
  :fetch-suggestions="querySearchAsync"
  placeholder="请输入要搜索的小区名字"
  @select="handleSelect"
></el-autocomplete>
  <el-button @click="queryAreaFunc">搜索一下</el-button>

  <el-table
      :data="tableData"
      style="width: 100%">

      <el-table-column
        prop="position"
        label="小区名"
        width="180" header-align="center">
      </el-table-column>

      <el-table-column
        prop="area_name"
        label="区域"
        width="180" header-align="center">
      </el-table-column>

	 <el-table-column
	 	sortable
        prop="area"
        label="面积/平方米"
        width="180" header-align="center">
      </el-table-column>

      <el-table-column
        sortable
        prop="price"
        label="单价"
        width="180" header-align="center">
      </el-table-column>

      <el-table-column
        sortable
        prop="total_price"
        label="总价"
        width="180" header-align="center">
      </el-table-column>

	  <el-table-column
        prop="floor_area"
        label="楼层区域"
        width="180" header-align="center">
      </el-table-column>
      
      <el-table-column
        prop="floor_count"
        label="楼层数"
        width="180" header-align="center">
      </el-table-column>

      <el-table-column
        prop="prop_title"
        label="特色"
        width="180" header-align="center">
      </el-table-column>
      
      <el-table-column
        prop="house_type"
        label="房子类型"
        width="180" header-align="center">
      </el-table-column>
      
      <el-table-column
        prop="direction"
        label="朝向"
        width="180" header-align="center">
      </el-table-column>
      
      
      <el-table-column
        sortable='custom'
        prop="build_date"
        label="建造时间"
        
        width="180" header-align="center">
      </el-table-column>
      <el-table-column
        prop="deal_type"
        label="交易状态"
        width="180" header-align="center">
      </el-table-column>
    </el-table>

<div class="block">
  <span class="demonstration"></span>
  <el-pagination
    layout="prev, pager, next"
    @current-change="page_change"
    :total="search_count"
    :current-page="current_page">
  </el-pagination>
</div>
</div>
</template>

<script type="text/javascript">
	export default {
		name: 'house_search',
		data () {
			return {
				restaurants: [],
        area: '',
        areas: [],
        search_count: 0,
        timeout:  null,
        table_columns: {
        	prop_title: '属性标题',
        	prop_tag: '标签',
        	total_price: '总价',
    		house_type: '房子类型',
    		area: '面积/平方米',
    		floor_area: '楼层区域',
    		floor_count: '楼层数',
    		direction: '朝向',
    		price: '单价',
    		position: '小区名',
    		area_name: '区域',
    		build_date: '建造时间',
    		deal_type: '交易状态'
        },
        tableData: [],
        current_page: 1,
        crawl_date: '',
        pickerOptions1: {
        	disabledDate(time){
        		return time.getTime() > Date.now();
        	}
        }
			}
		},
		mounted () {
			this.restaurants = this.loadAll();
		},
		methods: {
			loadAll() {
        return [
          { "value": "三全鲜食（北新泾店）", "address": "长宁区新渔路144号" },
          { "value": "Hot honey 首尔炸鸡（仙霞路）", "address": "上海市长宁区淞虹路661号" },
        ];
      },
      queryAreaFunc: function () {
      	var formData = JSON.stringify({'area': this.area, 'page': 1, 'limit':10})
      	this.$http.post('/api/area/get_search_result', formData).then(response => {
      		this.tableData = response.data.items
      		this.search_count = response.data.total
      		this.current_page = 1
      	})
      },
      page_change: function (current){
      	var formData = JSON.stringify({'area': this.area, 'page': current, 'limit':10})
      	this.$http.post('/api/area/get_search_result', formData).then(response => {
      		this.tableData = response.data.items
      		this.search_count = response.data.total
      		this.current_page = current
      	})
      },
      querySearchAsync(queryString, cb) {
      	if (this.area != ''){
      	var formData = JSON.stringify({'area': this.area})
      	this.$http.post('/api/area/get_search_result', formData).then(response => {
      		var results = response.data.items.map(function(item){
      			return {'value':item.position}
      		})
      		console.log(results)
      		clearTimeout(this.timeout)
      		this.timeout = setTimeout(() => {
      			cb(results)
      		}, 3000 * Math.random())
      	})
      }
      },
      createStateFilter(queryString) {
        return (state) => {
          return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelect(item) {
        console.log(item);
      }
		}
	}
</script>


